.carousel {
    position: relative;
  
    padding-bottom: 1rem;
    padding-top: 1rem;
    height: 90vh;
  
  }
  
  
  .image {
    width: 100%;
    height: 100%;
  
    object-fit: cover;
  }
  
  .carousel-arrow {
    position: absolute;
  
    height: 10rem;
    width: 5rem;
  
    border: none;
    background-color: inherit;
  
    top: 35%;
  
    font-size: 5rem;
    color: sienna;
  
    cursor: pointer;
  }
  
  .carousel-arrow:hover {
    color: springgreen;
  }
  
  .right-arrow {
    right: 5%;
  }
  
  .left-arrow {
    left: 5%;
  }
  
  .slide {
    opacity: 0;
  }
  
  .slide.active {
    height: 100%;
    opacity: 1;
  }
  
  @media screen and (max-width: 30rem) {
    .carousel {
      height: 30vh;
    }
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .slide.active {
      transition-duration: 1s;
    }
    .carousel:hover{
      transform: scale(0.9);
    }
  }